.chat-box{
      position: relative;  
      height: auto;
}

.scroll-down{
    opacity:1;
    position: absolute;
    height: 35px;
    width: 35px;
    bottom: 60px;
    right: 20px;
    cursor: pointer;
    transition: opacity 0.3s;

    
}

.none{
    opacity:0;    
    transition:opacity 0.3s linear;
}