.message{
    display: flex;
    flex-direction: column;
    margin-left:  5px;
    margin-top: 5px;
    margin-bottom: 15px;
    
   
}

.flex-end{
    align-items: flex-end;
}

.flex-start{
    align-items: flex-start;
}

.message:hover
.forward-arrow{
    display: inline;
    cursor: pointer;
}

.my-chat-message{
    color: white;
    border-radius: 3%;
    background: rgb(0, 120, 254);
    position: relative;
    margin-right: 15px;
    height: 30px;
    padding-top: 6px;
    padding-right: 15px;   
    padding-left: 5px;
}

.my-chat-message:before{
    content: "";
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: -8px;
    height: 20px;
    width: 20px;
    background: rgb(0, 120, 254);
    border-bottom-left-radius: 15px;
}

.my-chat-message::after{
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: -10px;
    width: 10px;
    height: 20px;
    background: white;
    border-bottom-left-radius: 10px;
}



.chat-message{
    background-color: #EEE;
    position: relative;
    padding-top: 6px;
    padding-left: 15px;
    margin-right: 10px;
    margin-left: 5px;
    margin-bottom: 5px;
    border-radius: 3%;
    height: 30px;
    padding-right: 15px; 
}

.chat-message:before{
    content: "";
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: -7px;
    height: 20px;
    width: 20px;
    background: #EEE;
    border-bottom-right-radius: 15px;
}

.chat-message::after{
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: -10px;
    width: 10px;
    height: 20px;
    background: white;
    border-bottom-right-radius: 10px;
}

.img{
    display: flex;
    width:100px;
    height: 100px;
    margin-left: 50px;
    margin-top: 10px;
    cursor: pointer;
}

[data-rmiz-modal-content]{
    
    top: 50% !important;
    left: 50% !important;
    margin-right: -50% !important;
    transform: scale(3.81818) translate(-45%, -30%) !important ; 
    transition: all 0s !important;
    
}
.MuiFormControlLabel-root{
    margin: 0 !important; 
    
}

.forward-arrow{
    display: none;
    height:15px;
    width:15px;
    margin-left: 7px;
}
.cancel-button{
    height:15px;
    width:15px;
    cursor: pointer;
}

.quote-title{
    font-style: italic;
    display: block;
}

.quote-name{
    display: block;
}

.quote{
    padding-right: 15px;
    display: block;
    display: flex;
    padding-left: 25px;
    margin-bottom: 15px;
    width: calc(100%-25px) ;
    background-color:#14315d1c !important;
    border-radius: 6%;
}
