.preview {
  position: relative;
  display: flex;
  justify-content: space-between;
  bottom: 0px;
  width: 100%;
  height: 80px;
  background-color: #14315d1c;
}
.quoted-title {
  margin: 0;
  margin-bottom: 8px;
}
.preview-close {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.preview-counter {
  margin-right: 80px;
  width: 50px;
  height: 50px;
  margin-top: 15px;
  border-radius: 100%;
  background-color: #757ce8;
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
}
